<template>
  <view class="container">
    <!-- #ifdef MP-WEIXIN -->
    <view
      v-if="!token"
      class="force-login-wrap"
    >
      <view class="force-login__content y-f">
		<image src="@/static/120x120.png" style="width: 100rpx; height: 100rpx;border-radius: 100rpx;"></image>
        <view class="login-notice">YSHOPÁîµÂïÜÁ≥ªÁªü</view>
        <button
          class="cu-btn author-btn"
          open-type="getPhoneNumber"
          @getphonenumber="phoneLogin"
		  v-if="isChecked"
        >ÊâãÊú∫Âè∑Âø´Êç∑ÁôªÂΩï</button>
		<button
		  v-else
		  class="cu-btn author-btn"
		  @tap="phoneLoginBefore"
		>ÊâãÊú∫Âè∑Âø´Êç∑ÁôªÂΩï</button>
        <button
          class="cu-btn close-btn"
          @tap="toLogin"
        >ÂÖ∂‰ªñÊâãÊú∫ÁôªÂΩï</button>
      </view>
    </view>
	<view class="hint">
		<radio  :checked="isChecked" @tap.stop="onChange" />
		ÊàëÂ∑≤ÁªèÈòÖËØªÂπ∂ÈÅµÂÆà
		<text class="link" @tap="goNewsDetail(50,'Áî®Êà∑ÂçèËÆÆ')">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>‰∏é
			<text class="link"  @tap="goNewsDetail(49,'ÈöêÁßÅÊîøÁ≠ñ')">„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
			<!--‰∏äÈù¢ÂçèËÆÆÊòØÂõ∫ÂÆöÁöÑ ËØ∑Ê∑ªÂä†Áõ∏Â∫îidÊñáÁ´†-->
	</view>
    <!-- #endif -->
    <!-- #ifndef MP-WEIXIN -->
    <view class="force-login-wrap">
      <!-- <image class="logo-bg" :src="`${$VUE_APP_RESOURCES_URL}/images/logo_bg.png`" mode="aspectFill"></image> -->
      <view class="force-login__content y-f">
        <view class="login-notice">‰∏∫‰∫ÜÊèê‰æõÊõ¥‰ºòË¥®ÁöÑÊúçÂä°ÔºåËØ∑ÂÖàÁôªÂΩï</view>
        <button
          class="cu-btn author-btn"
          @tap="toLogin"
        >ÂéªÁôªÂΩï</button>
      </view>
    </view>
    <!-- #endif -->
  </view>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import { wxappAuth, getUser, getUserInfo } from '@/api/user'
import { login, authorize, getProvider } from '@/utils'
import dayjs from 'dayjs'
import cookie from '@/utils/store/cookie'
import store from '@/store'

// ÂÖ¨‰ºóÂè∑ÁôªÂΩï
import { auth } from '@/libs/wechat.js'

export default {
  data() {
    return {
      authorize: false,
      canIUseGetUserProfile: false,
      code: '',
	  isChecked: false
    }
  },
  computed: {
    ...mapState(['isAuthorization', '$deviceType', 'token']),
  },
  onLoad() {
    if (wx.getUserProfile) {
      this.canIUseGetUserProfile = true
    }
    this.getLoginCode()

    // // ÂÖàÊ†°È™åÁî®Êà∑ÊòØÂê¶ÊéàÊùÉÔºåÂ¶ÇÊûúÊ≤°ÊúâÊéàÊùÉÔºåÊòæÁ§∫ÊéàÊùÉÊåâÈíÆ
  },
  mounted() {
    this.getLoginCode()
  },
  onHide() {
    this.updateAuthorizationPage(false)
    this.changeAuthorization(false)
  },
  onUnload() {
    this.updateAuthorizationPage(false)
    this.changeAuthorization(false)
  },
  methods: {
    ...mapActions(['changeAuthorization', 'setUserInfo']),
    ...mapMutations(['updateAuthorizationPage']),
	onChange() {
		console.log('isChecked;',this.isChecked)
		this.isChecked = !this.isChecked
	},
	goNewsDetail(id,name) {
	  this.$yrouter.push({
	    path: "/pages/shop/news/NewsDetail/index",
	    query: { id: id,name:name }
	  });
	},
    toLogin() {
      this.$yrouter.push({
        path: '/pages/user/Login/index',
        query: {},
      })
    },
    back() {
      this.$yrouter.switchTab({
        path: '/pages/home/index',
        query: {},
      })
    },
    getUserInfoBtn(data) {
      console.log(data)
      console.log('getUserInfoBtn')
      if (data.detail.errMsg == 'getUserInfo:fail auth deny') {
        uni.showToast({
          title: 'ÂèñÊ∂àÊéàÊùÉ',
          icon: 'none',
          duration: 2000,
        })
        return
      }
      uni.showLoading({
        title: 'ÁôªÂΩï‰∏≠',
      })
      login()
        .then(e => {
          uni.hideLoading()
          console.log('ÈáçÂÆöÂêë')
          this.$yrouter.reLaunch({
            path: cookie.get('redirect').replace(/\ /g, ''),
          })
        })
        .catch(error => {
          uni.hideLoading()
          console.log(error)
          uni.showToast({
            title: error,
            icon: 'none',
            duration: 2000,
          })
        })
    },
    // Áî≥ËØ∑Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    getUserInfoProfile(data) {
      console.log('getUserInfoProfile')
      wx.getUserProfile({
        lang: 'zh_CN',
        desc: 'ÈúÄË¶ÅËé∑ÂèñÊÇ®ÁöÑ‰ø°ÊÅØÁî®Êù•Â±ïÁ§∫',
        success: res => {
          console.log('Áî®Êà∑‰ø°ÊÅØ', res)
          uni.showLoading({
            title: 'ÁôªÂΩï‰∏≠',
          })
          login(res)
            .then(e => {
              console.log('ÈáçÂÆöÂêë')
              this.$yrouter.reLaunch({
                path: cookie.get('redirect').replace(/\ /g, ''),
              })
            })
            .catch(error => {
              console.log(error)
              uni.showToast({
                title: error,
                icon: 'none',
                duration: 2000,
              })
            })
        },
      })
    },
    getLoginCode() {
      getProvider()
        .then(provider => {
          // uniÁôªÂΩï
          uni.login({
            provider: provider,
            success: async loginRes => {
              console.log("üöÄ ~ file: index.vue:171 ~ getLoginCode ~ loginRes:", loginRes)
			  
              this.code = loginRes.code
			  //console.log('code11:',this.code)
            }
          })
        })
    },
    // ÂæÆ‰ø°Áî®Êà∑ÊâãÊú∫Âè∑ÁôªÂΩï
	phoneLoginBefore() {
		if(!this.isChecked) {  
			  uni.showToast({
				title: 'ËØ∑ÂãæÈÄâ‰∏ãÈù¢ÂçèËÆÆ',
				icon: 'none',
				duration: 2000,
			  })
			  return
		}
	},
    phoneLogin(e) {
      if (e.mp.detail.errMsg == 'getPhoneNumber:ok') {
        console.log('wxLoginCode', this.code)
        cookie.set('wxLoginCode', this.code)
        wxappAuth({
          encryptedData: e.mp.detail.encryptedData,
          iv: e.mp.detail.iv,
          code: this.code,
          spread: cookie.get('spread'),
        })
          .then(res => {
            console.log('ÁôªÂΩïÊàêÂäü,ÂºÄÂßãÂ§ÑÁêÜÁôªÂΩï‰ø°ÊÅØ‰øùÂ≠òÔºåÂπ∂Ëé∑ÂèñÁî®Êà∑ËØ¶ÊÉÖ')
            uni.hideLoading()
            store.commit('login', res.data.token, dayjs(res.data.expires_time))
            store.dispatch('userInfo', true)
            getUserInfo()
              .then(user => {
                console.log('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÊàêÂäü')
                uni.setStorageSync('uid', user.data.uid)
                store.dispatch('setUserInfo', user.data)
                this.$yrouter.reLaunch({
                  path: cookie.get('redirect').replace(/\ /g, ''),
                })
              })
              .catch(error => {
                console.log('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•')
                this.getLoginCode()
              })
          })
          .catch(error => {
            console.log(error)
            console.log('ÁôªÂΩïÊé•Âè£Ë∞ÉÁî®Â§±Ë¥•')
            this.getLoginCode()
          })
      } else {
        uni.showToast({
          title: 'Â∑≤ÊãíÁªùÊéàÊùÉ',
          icon: 'none',
          duration: 2000,
        })
      }
    },
  },
  mounted() {
    if (wx.getUserProfile) {
      this.canIUseGetUserProfile = true
      console.log(this.canIUseGetUserProfile)
    }
  },
}
</script>

